<template>
<h2>Latency 정보</h2>
<button @click='events.length = 0'>전부 지우기</button>
<ol>
  <li v-for="ev, i in events" :key=i>
    {{ev[0]}}: {{ (ev[1] - events[0][1]).toFixed(4) }}
  </li>
</ol>
</template>

<script>
export default {
  data: function() {
    return {
      events: [
      ],
      keyPressed: new Set(),
      lastKeyPressed: -1
    };
  },
  methods: {
    onkey (ev) {
      if (ev.repeat) return

      const now = (performance.now()) / 1000
      if (now - this.lastKeyPressed < 0.00) {
        this.lastKeyPressed = now
        // return
      }
      if (ev.type == 'keydown') {
        if (!this.keyPressed.has(ev.key)) {
          this.keyPressed.add(ev.key)
          this.events.push([`${ev.type}(${ev.key})`, now])
        }
      } else if (ev.type == 'keyup') {
        this.keyPressed.delete(ev.key)
        this.events.push([`${ev.type}(${ev.key})`, now])
      }
      this.lastKeyPressed = now
    }
  },
  mounted () {
    document.addEventListener('keydown', this.onkey)
    document.addEventListener('keyup', this.onkey)
  },
  beforeUnmount() {
    document.removeEventListener('keydown', this.onkey)
    document.removeEventListener('keyup', this.onkey)
  }
}
</script>

